.tableContainer {
  position: relative;
  z-index: 0;
}

.info {
  margin-top: 20px;
}

.error {
  margin-top: 8px;
}

.dropdownTh,
.dropdownTd {
  position: sticky;
  background-color: #ffffff;
  z-index: 1;
  left: 0;
  width: 44px;
  min-width: 44px;
}

.td {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.th, .td {
  &:nth-child(2) {
    padding-left: 0;
  }
}
