.container {
  display: flex;
  align-items: center;
  margin-top: 27px;
  position: relative;
}

.field {
  flex-grow: 1;
}

.canRemove {
  width: calc(100% - 25px);
  flex-grow: 0;
}

.iconWrapper {
  position: absolute;
  right: -5px;
  top: 15px;
  margin-left: 11px;

  &.withTitle {
    top: 39px;
  }
}

.icon {
  color: #FB233D;
}
