.tableContainer {
  position: relative;
  z-index: 0;
}

.info {
  margin-top: 20px;
}

.error {
  margin-top: 8px;
}

.actionTh,
.actionTd,
.dropdownTh,
.dropdownTd {
  position: sticky;
  background-color: #ffffff;
  z-index: 1;
}

.actionTh,
.actionTd {
  left: 0;
  width: 40px;
  min-width: 40px;
}

.dropdownTh,
.dropdownTd {
  left: 40px;
  width: 44px;
  min-width: 44px;
}

.td {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.th, .td {
  &:nth-child(3) {
    padding-left: 0;
  }
}

.checkbox {
  margin-top: 2px;
}
