.content {
  height: 100vh;
  overflow: hidden;
  display: grid;
  grid-template-columns: 50% 50%;
  box-sizing: border-box;
}

.col {
  overflow: auto;
}

.wrapper {
  padding: 40px 0;
  display: flex;
  flex-direction: column;
  width: 640px;
  margin: 0 auto;
}

.workflow {
  width: fit-content;
}

.header {
  display: flex;
  flex-wrap: nowrap;
  height: 72px;
}

.button {
  & + & {
    margin-left: 15px;
  }
}
