.wrapper {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  font-weight: 400;
}

.centerContent,
.rightContent {
  margin-left: 8px;
}

.centerContent {
  flex-grow: 1;
}

.iconWrapper,
.rightContent {
  flex-shrink: 0;
}

.title {
  margin: 0;
  font-size: 15px;
  line-height: 20px;
  color: #262626;
  font-weight: 600;
}

.description {
  margin: 0;
  font-size: 13px;
  line-height: 16px;
  color: #262626B2;
}

.title + .description {
  margin-top: 2px;
}

.cardStatus {
  margin: 0;
  font-size: 13px;
  line-height: 16px;
  text-align: right;
  color: #262626B2;
}

.balance {
  margin: 0;
  text-align: right;
  font-size: 15px;
  line-height: 20px;
  font-weight: 600;
}

.icon {
  width: 36px;
  height: 36px;
  min-width: 36px;
  min-height: 36px;
}