.container {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  font-weight: 400;
  font-size: 13px;
}

.title,
.dateTime,
.amount,
.creditStatus {
  margin: 0;
}

.title,
.amount {
  font-weight: 600;
  font-size: 15px;
}

.dateTime,
.creditStatus {
  color: #262626b2;
}

.title,
.amount {
  margin-bottom: 2px;
}

.title {
  white-space: normal;
  margin-right: 5px;
}

.amount, .creditStatus {
  text-align: right;
}

.creditStatus {
  max-width: 280px;
}

.leftContent {
  width: 50%;
  flex-grow: 1;
}
