.container {
  & + & {
    margin-top: 24px;
  }
}

.content {
  border: 1px dashed rgba(35, 35, 35, 0.24);
  padding: 16px;
  border-radius: 4px;
}

.fields {
  margin-top: 32px;
}

.field {
  width: 100%;
}

.actions {
  display: flex;
  justify-content: flex-end;
  margin-top: 8px;
}

.action {
  & + & {
    margin-left: 20px;
  }
}
